/* ================================================
--> Section divider / hr style
================================================== */
hr.hr_narrow {
    width: 7%;
    height: 2px;
    height: 20px 0;
}
.hr_color, .hr_color hr {
    color: $white-color;
    background: $white-color;
}

hr.hr-inline{
  display: inline-block;
}
/* ================================================
--> Elements footer style
================================================== */
.footer.element-footer {
